html {
  scroll-behavior: smooth;
}

.MainContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: noto-sans-cjk-kr,sans-serif;
  /* font-family: "Malgun Gothic"; */
  font-size: 16px;
}

.MainBanner {
  width: 100%;
  height: 640px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f8f8f8;
}

.MainRecipeRank {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
}

#RecipeTitle, #DietTitle {
  font-size: 24px;
  font-weight: 500;
  padding: 4px 0;
}

#RecipeSubTitle, #DietSubTitle{
  color: #999999;
}

.MainRecipeRankContent, .MainDietRankContent{
  display: flex;
  justify-content: center;
  align-content: center;
  padding: 20px 0;
}

.MainDietRankContent .MainRecipeImg {
  background-color: #f8f8f8;
}

.MainRecipeRankBox {
  width: 320px;
  height: 320px;
  padding: 30px;
  transition: all 0.2s linear;
  cursor: pointer;
}

.MainRecipeRankBox:hover {
  transform: scale(1.1, 1.1);
}

.MainRecipeImg {
  width: 320px;
  height: 240px;
  background-color: #f8f8f8;
}

.MainRecipeImg img {
  width: 320px;
  height: 240px;
}

.MainRecipeName {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.MainRecipeName span:nth-child(1) {
  font-size: 16px;
  padding: 4px 0;
}

.MainRecipeName span:nth-child(2) {
  padding: 4px 8px;
}

.MainRecipeHashtag {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

.MainRecipeHashtag span {
  font-size: 14px;
  width: auto;
  padding: 4px;
  padding-right: 16px;
  color: #999999;
}

.MainNewRecipe {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 0;
  background-color: #f8f8f8;
}

.MainNewRecipeBox {
  width: 1440px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 60px 0;
}

.MainNewRecipeImg {
  width: 420px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
}

.MainNewRecipeInfo {
  width: 420px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.viewBtn {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  margin-top: 40px;
}

.RevViewBtn {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  margin-top: 40px;
}

.viewBtn #btn, .RevViewBtn #btn {
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  border-radius: 100%;
  outline: 1px solid #c1c1c1;
  cursor: pointer;
  transition: color 0.3s ease-in-out;
}

#btn:hover {
  color: rgb(50, 220, 199);
}

.MainNewRecipe #recipeDescription {
  color: #999999;
}

.MainNewRecipeName {
  display: flex;
  flex-direction: row;
  padding: 8px 0;
  align-items: center;
}

.MainNewRecipeName span {
  font-size: 20px;
  font-weight: 500;
  padding-right: 8px;
}

.MainRevNewRecipeBox {
  width: 1440px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 40px 0;
}

.MainAllViewBtn, .MainCreateBtn{
  width: 120px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #999999;
  border-radius: 30px;
  background-color: #ffffff;
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

.MainSearchForm {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: noto-sans-cjk-kr,sans-serif;
  font-size: 16px;
  padding: 40px 0;
} 

.MainSearchForm #searchTitle {
  font-size: 24px;
  font-weight: 500;
}

.MainSearchForm #searchSubTitle {
  padding: 4px;
  color: #999999;
}

.MainSearchBox {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;  
  padding: 20px 0;
}

.MainSearchBox select {
  width: 100px;
  height: 50px;
  border: 1px solid #999999;
  outline: none;
  margin: 0 10px;
  padding: 0 16px;
  font-size: 14px;
}

.MainInputKeyword {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.MainInputKeyword #searchIcon {
  color: rgb(153, 153, 153);
  position: absolute;
  left: 150px;
} 

.MainInputKeyword input {
  width: 620px;
  height: 50px;
  padding: 0 60px; 
  border: 1px solid #999999;
  box-sizing: border-box;
  outline: none;
  margin: 0 10px;
  font-size: 16px;
}

.MainInputKeyword input:focus {
  border: 1px solid rgb(50, 220, 199);
}

.MainInputKeyword input:-webkit-autofill {
  box-shadow: 0 0 0 1000px #fff inset;
}

.MainKeywordForm {
  width: 600px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.MainKeywordList {
  width: auto;
  height: 30px;
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 8px;
  border: 1px solid #999999;
  cursor: pointer;
}

.MainKeywordForm #all {
  border: 1px solid rgb(50, 220, 199);
}

.MainKeywordForm #all span {
  color: rgb(50, 220, 199);
  padding: 16px;
}

.MainKeywordForm #value span {
  color: rgb(153, 153, 153);
  padding: 16px;
}

.MainSearchResultForm, .MainSearchRecipeResultForm, .MainSearchDietResultForm {
  width: 60%;
  min-height: 480px;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
  align-items: center;
}


.MainDietRank {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: #f8f8f8; */
  padding: 40px 0;
}

.MainDietCreate {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
}

.MainRegi {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
}