.DailyMealsForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: noto-sans-cjk-kr,sans-serif;
    font-size: 16px;
}

.DailyMealsSearchBox {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;  
    padding: 20px 0;
}

.DailyMealsSearchForm {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: noto-sans-cjk-kr,sans-serif;
    font-size: 16px;
    padding: 80px 0;
} 
  
.DailyMealsSearchForm #searchTitle {
    font-size: 24px;
    font-weight: 500;
}
  
.DailyMealsSearchForm #searchSubTitle {
    padding: 16px;
    color: #999999;
}

.DailyMealsInputKeyword {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
  
.DailyMealsInputKeyword #searchIcon {
    color: rgb(153, 153, 153);
    position: absolute;
    left: 30px;
} 
  
.DailyMealsInputKeyword input {
    width: 720px;
    height: 50px;
    padding: 0 60px; 
    border: 1px solid #999999;
    box-sizing: border-box;
    outline: none;
    margin: 0 10px;
    font-size: 16px;
  }
  
.DailyMealsInputKeyword input:focus {
    border: 1px solid rgb(50, 220, 199);
}
  
.DailyMealsInputKeyword input:-webkit-autofill {
    box-shadow: 0 0 0 1000px #fff inset;
}

.DailyMealsKeywordForm {
    width: 600px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
  
.DailyMealsKeywordList {
    width: auto;
    height: 30px;
    border-radius: 30px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 8px;
    border: 1px solid #999999;
    cursor: pointer;
}
  
.DailyMealsKeywordForm #all {
    border: 1px solid rgb(50, 220, 199);
}
  
.DailyMealsKeywordForm #all span {
    color: rgb(50, 220, 199);
    padding: 16px;
}
  
.DailyMealsKeywordForm #value span {
    color: rgb(153, 153, 153);
    padding: 16px;
}

.DailyMealsSearchResultForm {
    width: 100%;
    min-height: 640px;
    height: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
}

.DailyMealsSearchResultForm span {
    cursor: default;
}