.detailRecipeView {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 80px 0;
  font-family: noto-sans-cjk-kr,sans-serif;
}

.recipeInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 1440px;
}

.recipeInfo #recipeImg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 420px;
  height: 420px;
  /* border-radius: 30px; */
  background-color: #f8f8f8;
}

#recipe {
  width: 540px;
  height: 420px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.recNo {
  font-size: 20px;
  padding: 20px 0;
  /* padding-bottom: 20px; */
}

.recNo #name {
  /* padding-left: 16px; */
}

.recDes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 20px 0;
}

.recDes #description {
  /* font-size: 16px; */
  padding-top: 8px;
}

.recipeClass {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 20px 0;
}

.recipeClass #recClass {
  padding-top: 8px;
}

.recipeIngredient {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
}

.recipeIngredient #recIngredient {
  padding-top: 8px;
}

.cookingInfo {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 100px;
}

.recTitle {
  padding-bottom: 60px;
}

.recTitle #name {
  font-size: 24px;
}

.cookingOrder {
  width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: top;
  padding: 40px 0;
  /* background-color: #f8f8f8; */
}

.cookingDes {
  width: 640px;
  display: flex;
  flex-direction: column;
}

.cookingDes #cookingOrder {
  font-size: 20px;
  padding: 20px 0;
}

.cookingDes #cookingDescription {
  padding-left: 24px;
  padding-bottom: 20px;
}

.cookingImg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 360px;
  height: 360px;
  /* border-radius: 30px; */
  background-color: #f8f8f8;
}

.sideDishInfo {
  width: 1200px;
  height: 480px;
  display: flex;
  flex-direction: column;
}

.sideDishInfo #name {
  font-size: 20px;
  padding: 30px 0;
}

.sideDishImg {
  display: flex;
  flex-direction: row;
}

.sideDish {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 240px;
  height: 240px;
  background-color: #f8f8f8;
  /* border-radius: 30px; */
  margin-right: 40px;
}