.sel.clicked {
  color: gold;
}

.layout{
  width: 100%;
  font-family: noto-sans-cjk-kr,sans-serif;
  display: flex;
  flex-direction: column;
}

.title1, .title2, .title3{
font-size: 20px;
font-weight: bold;
display: flex;
align-items: center;
}

.conList{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  overflow: auto;
  overflow-y:hidden;
  white-space: nowrap;
}

