.DietCreateContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 80px 0;
  font-family: noto-sans-cjk-kr,sans-serif;
  font-size: 16px;
}

.RecipeSelectBox {
  width: 1080px;
  display: flex;
  justify-content: space-between;
  padding: 80px 0;
}

.Recipe {
  display: flex;
  flex-direction: column;
}

.Recipe #time {
  font-size: 20px;
  font-weight: 500;
  color: rgb(50, 220, 199);
  padding: 16px 0;
}

.Recipe #recipeName .Recipe #recipeComment {
  font-size: 16px;
}

.Recipe span {
  padding: 4px 0;
}

.RecipeName {
  display: flex;
  flex-direction: column;
  padding: 8px 0;
}

.RecipeName span {
  padding: 4px 0;
}

.RecipeComment {
  display: flex;
  flex-direction: column;
  padding: 4px 0;
}

.RecipeComment span {
  padding: 4px 0;
}

.RecipeImgBox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 320px;
  height: 320px;
  background-color: #f8f8f8;
  cursor: pointer;
}

.DietInfoBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 1080px;
  padding: 60px 0;
  /* align-items: flex-start; */
}

.DietInfoBox #dietInfoInput {
  font-size: 20px;
  font-weight: 500;
  color: rgb(50, 220, 199);
  padding: 20px 0;
}

.DietName {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 20px 0;
}

.DietName span {
  font-size: 20px;
  font-weight: 500;
}

.DietName input {
  width: 240px;
  margin: 16px;
  border: none;
  border-bottom: 1px solid #999999;
  font-size: 16px;
}

.DietComment {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
}

.DietComment span {
  font-size: 20px;
  font-weight: 500;
  padding: 16px 0;
}

.DietComment textarea {
  height: 240px;
  font-size: 16px;
}

.DietHashTag {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
}

.HashTagOuter {
  height: 20px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0;
}

.hashTag {
  display: flex;
  flex-direction: column;
  margin: 0px 10px;
}

.hashTagForm {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-width: 60px;
  padding: 0 4px;
  margin: 0 8px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.hashTagForm label {
  font-size: 16px;
  padding-left: 10px;
  padding-right: 10px;
  color: #999999;
}

.hashBtnForm {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  color: #c1c1c1;
}

.hashBtnForm .delBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  cursor: pointer;
  transition: color 0.3s ease-in-out;
  position: relative;
  top: 10px;
  right: 5%;
}

.delBtn i {
  font-size: 12px;
}

.delBtn:hover {
  color: rgb(255, 59, 48);
}

.DietHashTag span {
  font-size: 20px;
  font-weight: 500;
  padding: 4px 0;
}

.DietHashTag #sub {
  font-size: 14px;
  font-weight: 400;
  color: #999999;
}

.DietHashTag input {
  margin: 20px 0;
  border: none;
  border-bottom: 1px solid #999999;
  font-size: 16px;
}

input:focus {
  outline: none;
  border-bottom: 1px solid rgb(50, 220, 199);
}

input:-webkit-autofill, textarea:-webkit-autofill{
  box-shadow: 0 0 0 1000px #fff inset;
}

textarea {
  resize: none;
  border: 1px solid #999999;
}

textarea:focus {
  outline: none;
  border: 1px solid rgb(50, 220, 199);
}

.RegiBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  cursor: pointer;
}

.RegiBtn div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 40px;
  border: 1px solid #999999;
  border-radius: 30px;
}