label { 
    font-size: 14px;
}

.MyFridgeForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: noto-sans-cjk-kr,sans-serif;
    font-size: 13px;
    background-color: #FFFFFF;
}

.FridgeContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.FridgeRegiForm {
    width: 1280px;
    min-height: 640px;
    /* max-height: 640px; */
    padding: 40px 0;
}

.FridgeRegiForm span {
    font-size: 20px;
}

.FridgeSearchForm {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    padding: 20px 0;
}

.FridgeSearchForm select {
    height: 40px;
    border: 1px solid #999999;
    outline: none;
    margin: 0 10px;
    padding: 0 16px;
    font-size: 14px;
}

.FridgeSearchForm #searchCategory1 {
    width: 100px;
}

.FridgeSearchForm #searchCategory2 {
    width: 140px;
}

.FridgeSearchForm .SearchBtn {
    width: 80px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(50, 220, 199);
    margin: 0 10px;
    cursor: pointer;
}

.FridgeSearchForm .SearchBtn span {
    color: #FFFFFF;
    font-size: 14px;
}

.FridgeRegiResultBox {
    /* display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start; */
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
}

.FridgeRegiResultBox span {
    font-size: 14px;
}

.getIngredientItemForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    cursor: pointer;
}

.getIngredientItemForm label {
    cursor: pointer;
}

.ingredientItemForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    cursor: pointer;
    margin: 10px 0;
}

.ingredientItem, .getIngredientItem {
    width: 400px;
    height: 120px;
    /* display: flex;
    justify-content: center;
    align-items: center; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
    /* margin: 20px 0; */
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.ingredientItem #img {
    width: 100px;
    height: 100px;
    background-color: #f8f8f8;
    margin: 0 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.ingredientItem #fileUpload {
    width: 160px;
    align-self: flex-start;
}

.ingredientInfoForm {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.ingredientInfoForm span {
    /* padding: 8px 0; */
    /* cursor: default; */
}

.ingredientInputForm {
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.ingredientInputForm #shelfLifeForm, .ingredientInputForm #ingredientNameForm {
    width: 180px;
    /* display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center; */
    display: grid;
    grid-template-columns: 1fr 1fr;
}

#shelfLifeForm input, #ingredientNameForm input {
    width: 100px;
    border: none;
    border-bottom: 1px solid #c1c1c1;
    box-sizing: border-box;
    outline: none;
    font-size: 16px;
    text-indent: 4px;
    margin: 0 8px;
}

#shelfLifeForm input:focus, #ingredientNameForm input:focus {
    border-bottom: 1px solid rgb(50, 220, 199);
}
  
#shelfLifeForm input:-webkit-autofill, #ingredientNameForm input:-webkit-autofill {
    box-shadow: 0 0 0 1000px #fff inset;
}

#shelfLifeLimitForm {
    width: 180px;
    /* display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center; */
    display: grid;
    grid-template-columns: 1fr 0.05fr 0.95fr;
}

#shelfLifeLimitForm {
    font-size: 14px;
    color: #c1c1c1;
}

#shelfLifeLimitForm #time {
    padding: 0 4px;
}

#timeLimitForm {
    width: 180px;
    display: grid;
    grid-template-columns: 1fr;
}

#timeLimitForm label {
    font-size: 14px;
}

.editForm {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ingredientCount {
    display: flex;
    flex-direction: row;
    width: 100px;
    justify-content: center;
    align-items: center;
}

.ingredientCount input {
    width: 40px;
    height: 20px;
    padding: 0;
    margin: 0 8px;
    text-indent: 8px;
}

.ingredientCount input:focus {
    border: 1px solid rgb(50, 220, 199);
}

.ingredientCount input:-webkit-autofill {
    box-shadow: 0 0 0 1000px #fff inset;
}

.ingredientCount span {
    padding: 0 4px;
}

.ingredientInputForm #storeInfo {
    width: 180px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px 0;
}

.ingredientInputForm #storeInfo label {
    padding: 0 8px;
    font-size: 13px;
}

.ingredientInputForm #ingredientBtn {
    width: 80px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
}

.ingredientInputForm #delBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 20px;
    color: red;
    cursor: pointer;
    font-size: 14px;
}

.ingredientInputForm #addBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 20px;
    color: rgb(50, 220, 199);
    cursor: pointer;
    font-size: 14px;
}

.btnForm {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    color: #c1c1c1;
}

.btnForm i {
    font-size: 16px;
    padding: 0 8px;
}

.deleteBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    cursor: pointer;
    transition: color 0.3s ease-in-out;
    position: relative;
    top: 60%;
    right: 10%;
}

.editBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    cursor: pointer;
    transition: color 0.3s ease-in-out;
    position: relative;
    top: 60%;
    right: 30%;
}

.okBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    cursor: pointer;
    transition: color 0.3s ease-in-out;
    position: relative;
    top: 60%;
    right: 30%;
}

.btnForm {
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.btn {
    width: 100px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    background-color: #ffffff;
    color: rgb(193, 193, 193);
    border: 1px solid rgb(193, 193, 193);
    /* box-shadow: 0 2px 5px rgba(0,0,0,0.1); */
    cursor: pointer;
    transition: color 0.2s ease-in-out;
    margin: 0 20px;
    cursor: pointer;
}
  
.btn span {
    font-size: 14px;
    /* color: rgb(193, 193, 193); */
}
  
.btn:hover {
    color: rgb(50, 220, 199);
    border-color: rgb(50, 220, 199);
}


.deleteBtn:hover {
    color: rgb(255, 59, 48);
}

.editBtn:hover, .okBtn:hover {
    color: rgb(50, 220, 199);
}

.IngredientAddBtnForm {
    margin: 10px 0;
}

.IngredientAddBtnForm .btnForm {
    visibility: hidden;
}

.IngredientAddBtn {
    width: 400px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    cursor: pointer;
    /* margin: 20px 0; */
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

#circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    background-color: #ffffff;
    border-radius: 100%;
}

#circle i {
    color: rgb(50, 220, 199);
    font-size: 24px;
}

.pageBtn {
    width: 100%;
    height: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pageBtn #circle {
    width: 8px;
    height: 8px;
    border-radius: 100%;
    background-color: rgb(50, 220, 199);
    cursor: pointer;
    margin: 0px 4px;
}

.ExpiryItemForm {
    margin: 40px 0;   
}

.ExpiryDateForm {
    width: 1280px;
    height: 200px;
    padding: 40px 0;
}

.ExpiryDateForm span {
    font-size: 20px;
}

.ExpiryDateBox {
    height: 200px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
}

.ExpiryDateBox span {
    font-size: 14px;
}

.SelectedIngredientsForm {
    width: 1280px;
    min-height: 200px;
    padding: 40px 0;
}

.SelectedIngredientsForm span {
    font-size: 20px;
}

.SelectedIngredientsBox {
    /* height: 200px; */
    min-height: 200px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
}

.SelectedIngredientsBox span {
    font-size: 14px;
}

.RecipeResultForm {
    width: 1280px;
    min-height: 480px;
    display: flex;
    flex-direction: column;
}

.RecipeResultForm span {
    font-size: 20px;
}

.RecipeResultBox {
    min-height: 400px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
}

@media screen and (max-width: 980px) {
    .FridgeRegiForm {
      width: 840px;
    }

    .ExpiryDateForm {
        width: 840px;
    }

    .FridgeRegiResultBox {
        grid-template-columns: repeat(2, 1fr);
    }

    .ExpiryDateBox {
        grid-template-columns: repeat(2, 1fr);
    }
}
  
@media screen and (max-width: 768px) {
    .FridgeRegiForm {
        width: 640px;
    }

    .ExpiryDateForm {
        width: 640px;
    }

    .FridgeSearchForm {
        width: 530px;
    }

    .FridgeRegiResultBox {
        grid-template-columns: repeat(1, 1fr);
    }

    .ExpiryDateBox {
        grid-template-columns: repeat(1, 1fr);
    }

    .RecipeSearchResultForm {
        width: 80%;
        min-height: 640px;
        height: auto;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    
    .RecipeSearchResultForm span {
        cursor: default;
    }
}