.JoinFirstForm {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: noto-sans-cjk-kr,sans-serif;
    font-size: 16px;
}

.JoinFirstForm .navLogo {
    font-size: 30px;
    padding: 40px 0;
}

.JoinFirstContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.JoinFirstContent span {
    font-size: 14px;
}

.JoinFirstContent span:nth-child(even) {
    padding: 10px 0;
    color: rgb(153, 153, 153);
}

.JoinNextBtn {
    width: 360px;
    height: 50px;
    margin: 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(50, 220, 199);
    color: #ffffff;
    cursor: pointer;
}