.MyPageContent {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: noto-sans-cjk-kr,sans-serif;
  font-size: 13px;
  background-color: #FFFFFF;
}

.MyPageForm {
  width: 1280px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
}

.MyPageForm label {
  font-size: 24px;
  align-self: flex-start;
}

.UserSettingForm {
  width: 1280px;
  padding: 40px 0;
}

.UserSettingForm label {
  font-size: 20px;
}

.UserInfoForm {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.UserInfoForm #user {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}

.UserInfoForm #user label:nth-child(odd) {
  width: 120px;
}

.UserInfoForm #user label:nth-child(even) {
  width: 100px;
}

.UserInfoForm #removeUser {
  color: rgb(193, 193, 193);
}

.UserInfoForm #removeUser label {
  font-size: 14px;
  cursor: pointer;
}

.UserInfoForm label {
  font-size: 18px;
  padding: 10px 20px;
}

.btn {
  width: 100px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  background-color: #ffffff;
  color: rgb(193, 193, 193);
  border: 1px solid rgb(193, 193, 193);
  /* box-shadow: 0 2px 5px rgba(0,0,0,0.1); */
  cursor: pointer;
  transition: color 0.2s ease-in-out;
  margin: 0 20px;
  cursor: pointer;
}

.btn span {
  font-size: 14px;
  /* color: rgb(193, 193, 193); */
}

.btn:hover {
  color: rgb(50, 220, 199);
  border-color: rgb(50, 220, 199);
}

.UserRegiInfoForm {
  width: 1280px;
  min-height: 320px;
  padding: 40px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.UserRegiInfoForm label {
  font-size: 16px;
  align-self: center;
}