.fir {
    margin: 0 auto;
    width: 1400px;
    font-family: noto-sans-cjk-kr,sans-serif;
    display: flex;
    flex-direction: column;
}

.kan {
    display: flex;
}

.cold-border,
.frozen-border,
.room-border {
    width: 467px;
    height: auto;
    float: left;

}

.cold,
.frozen,
.room {
    overflow: auto;
    width: 400px;
    height: 532px;
    margin: 15px;
    border-left: solid 8px #32dcc7;
    border-top-left-radius: 7px;
    /* border-top-right-radius: 50px; */
    border-bottom-left-radius: 7px;
    /* border-bottom-right-radius: 50px; */
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
}

/* ìŠ¤í¬ë¡¤ë°” ì„¤ì •*/
.cold::-webkit-scrollbar,
.frozen::-webkit-scrollbar,
.room::-webkit-scrollbar {
    width: 0px;
}

/* ìŠ¤í¬ë¡¤ë°” ë§‰ëŒ€ ì„¤ì •*/
.cold::-webkit-scrollbar-thumb,
.frozen::-webkit-scrollbar-thumb,
.room::-webkit-scrollbar-thumb{
    height: 17%;
    background-color: #010202;
    border-radius: 30px;
}

