.name {
    font-weight: bold;
    display: flex;
    font-size: 15px;
    justify-content: center;
    align-items: center;
    margin-right: 100px;
}

.namePlus {
    display: flex;
    align-items: baseline;
    justify-content: flex-end;
    margin-right: 50px;
}

@media screen and (max-width: 980px) {
    .fir {
        width: 800px;
    }

}

@media screen and (max-width: 768px) {
    .fir {
        width: 640px;
    }
}

@media screen and (max-width: 480px) {
    .fir {
        width: 320px;
    }
}