@font-face {
  font-family: 'NanumSquareRound';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/NanumSquareRound.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

.item {
  font-size: 13px;
  display: flex;
  padding: 10px;
  width: 430px;
  height: 101px;
  align-items: center;
  

}

.img {
  display: flex;
  width: 110px;
  height: 110px;
  border: 3px solid #32DCC7;
  border-radius: 15%;
}

.info {
  display: flex;
  flex-direction: column;
  margin-left: 16px;
}

.first {
  display: flex;
  align-items: baseline;
}

.date {
  margin-right: 77px;
}

.del {
  margin-right: 22px;

}

/* .chek{

} */

.second {
  display: flex;
  align-items: baseline;
  margin-top: 10px;
  flex-direction: row;
  justify-content: space-between;

}

.proName {
  margin-right: 10px;
  display: flex;
  flex-direction: column;
}

.num{
  display: flex;
  flex-direction: column;
}

.dead {
  margin-top: 10px;
  align-items: baseline;
  justify-content: start;
  display: flex;
}
.numOpsion{
  display: flex;
}
.nameInput{
  width: 118px;
}
.numInput{
  width: 30px;
}
select{
  width: 45px;
  margin-left: 5px;
}