.FindIdForm {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: noto-sans-cjk-kr,sans-serif;
    font-size: 16px;
}
  
.FindIdForm .navLogo {
    font-size: 30px;
    padding: 40px 0;
}

.FindIdTitle {
    width: 360px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px 0;
}

.FindIdForm input {
    width: 360px;
    height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    /* border: 1px solid #999999; */
    border: none;
    border-bottom: 1px solid #999999;
    box-sizing: border-box;
    outline: none;
    font-size: 16px;
    margin: 10px 0;
    text-indent: 10px;
}

.FindIdForm input:focus {
    border-bottom: 1px solid rgb(50, 220, 199);
}
  
.FindIdForm input:-webkit-autofill {
    box-shadow: 0 0 0 1000px #fff inset;
}

.FindIdNextBtn {
    width: 360px;
    height: 50px;
    margin: 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(50, 220, 199);
    color: #ffffff;
    cursor: pointer;
}