.DetailDietViewBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  font-family: noto-sans-cjk-kr,sans-serif;
}

.DetailDietView {
  width: 1440px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 40px 0;
}

.DetailRecipeBox {
  width: 1440px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 40px 0;
}

.DetailRecipeInfo {
  width: 420px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

#timeTitle {
  padding: 16px 0;
  font-size: 20px;
  font-weight: 500;
  color: rgb(50, 220, 199);
}

#detailRecipeName {
  padding: 16px 0;
  font-size: 16px;
}

#detailRecipeDes {
  padding: 16px 0;
  font-size: 16px;
}

.DetailRecipe {
  cursor: pointer;
  display: flex;
  flex-direction: row;
}

.DetailRecipeImg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 320px;
  height: 320px;
  background-color: #f8f8f8;
}

.DetailDietInfoBox {
  width: 1080px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 40px 0;
}

.DetailDeitInfoView {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.DetailDeitInfoView #dietInfo {
  font-size: 20px;
  font-weight: 500;
}

.DetailDietName {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 20px 0;
}

.DetailDietName span {
  font-size: 20px;
  font-weight: 500;
  padding-right: 16px;
}

.DetailDietDes {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-height: 200px;
  padding: 20px 0;
}

.DetailDietDes span:nth-child(1) {
  font-size: 20px;
  font-weight: 500;
}

.DetailDietDes span {
  padding: 8px 0;
}

.DetailDietKeyword {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.DetailDietKeyword #dietKeyword {
  font-size: 20px;
  font-weight: 500;
  padding: 16px 0;
}

.DetailDietKeywordList {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.DetailDietKeywordList span {
  padding-right: 16px;
}